<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">订单详情</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in list" @click="selectItem(item)">
              <div class="top">
                <span>{{item.time}}</span>
                <span>{{item.tradeNo}}</span>
              </div>
              <div class="middle">
                <p class="title">账单总额</p>
                <p class="price">￥<span>{{item.total}}</span></p>
                <ul class="detail">
                  <li class="detail-item">
                    <label>代扣还款</label>
                    <span>￥{{item.daikou}}</span>
                  </li>
                  <li class="detail-item">
                    <label>剩余应还款</label>
                    <span>￥{{item.should}}</span>
                  </li>
                  <li class="detail-item">
                    <label>剩余补贴额度</label>
                    <span>￥{{item.left}}</span>
                  </li>
                </ul>
              </div>
              <div class="bottom">
                <label>订单ID</label>
                <span>{{item.orderId}}</span>
                <i class="forward" @click="goToMyOrderDetail"></i>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'

  export default {
    data () {
      return {
        query: '',
        list: []
      }
    },
    created () {
      this._getPayAccountList()
    },
    methods: {
      back() {
        this.$router.back()
      },
      goToMyOrderDetail() {
        this.$router.push({
          path: `${this.$router.currentRoute.path}/orderdetail`
        })
      },
      showCategory() {

      },
      selectItem(item) {
        console.log(item)
      },
      _getPayAccountList() {
        for(let i = 0; i < 1; i++){
          let obj = {
            time: '2018-02-07',
            tradeNo: '2018020710000',
            total: '2300',
            daikou: '100',
            should: '200',
            left: '2000',
            orderId: '61056890245788',
          }
          this.list.push(obj)
        }
      },
      onQueryChange(newQuery) {
        this.query = newQuery
        //dosomething
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
   .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      .content
        height 100%
        overflow hidden
        margin-left 15px
        margin-right 15px
        .list
          width 100%
          .item
            display flex
            flex-direction column
            align-items center
            height 278px
            box-sizing border-box
            padding-left 15px
            padding-right 15px
            margin-top 10px
            background #fff
            border-radius 3px
            border 1px solid #e6e6e6
            &:last-child
              margin-bottom 10px
            .top
              width 100%
              display flex
              justify-content space-between
              margin-top 18px
              color #666
              font-size $font-size-small-x
            .middle
              margin-top 30px
              width 100%
              .title
                text-align center
                font-size $font-size-small-x
                color #999
              .price
                text-align center
                color #333
                font-weight bold
                font-size $font-size-small-x
                margin-top 16px
                margin-bottom 16px
                span
                  font-size $font-size-large-m
                  font-weight bold
              .detail
                display flex
                flex-direction column
                width 100%
                .detail-item
                  height 32px
                  display flex
                  align-items center
                  label
                    font-size $font-size-small-x
                    color #999
                    width 120px
                  span
                    font-size $font-size-medium
                    color #333
            .bottom
              display flex
              width 100%
              flex 1
              margin-top 10px
              align-items center
              border-top 1px solid #f2f2f2
              position relative
              label
                font-size $font-size-small-x
                color #999
                width 60px
              span
                font-size $font-size-medium
                color #333
              .forward
                background url("~common/image/forward.png") no-repeat
                background-size contain
                margin-left 18px
                width 6px
                height 10px
                position absolute
                right 0
                &:before
                  position absolute
                  left -10px
                  top -10px
                  right -10px
                  bottom -10px
                  content ''

</style>



